
.SM-container-wallet {
    background: #161D26;
    padding-left: 1.5rem;


}

.SM-container-wallet .SM-part-one-wallet{
display: flex;
flex-direction: column;
padding-left: 1rem;
padding-top: 25px;

}

.SM-container-wallet .SM-part-one-wallet .SM-start-wallet{
    display: flex;
}
.SM-container-wallet .SM-part-one-wallet .SM-start-wallet p{
    font-family: Poppins;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF;

}
.SM-part-one-wallet .SM-paths-wallet{
    display: flex;
    gap: 5px;
}
.SM-container-wallet .SM-part-one-wallet .SM-paths-wallet p,.SM-part-two-wallet .SM-Connect-wallet p{
    font-family: Poppins;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0em;
    text-align: justified;
    color: #A4A8AB;
}
.SM-container-wallet .SM-part-one-wallet .SM-paths-wallet img{
    width: 14px;
    height: 14px;

}
.SM-white{
    color:#FFFFFF !important;
}
.SM-sizeXL{

font-size: 30px !important;
font-weight: 700 !important;

}
.SM-part-two-wallet{
    display: flex;
    flex-direction: column;
}
.SM-container-wallet .SM-part-two-wallet .SM-Connect-wallet{

    display: flex;
    flex-direction: column;
}
.SM-part-two-wallet .SM-Connect-wallet p{
font-size: 16px;
text-align: left;
}
.SM-part-two-wallet .SM-items-wallet{
    display: flex;
    flex-wrap: wrap;
    gap: 60px

}